@use './util' as u;

@font-face {
  font-family: 'DM Sans';
  src: url('../../assets/fonts/DMSans-VariableFont_opsz\,wght.ttf')
    format('truetype');
}

@font-face {
  font-family: 'DM Sans';
  font-style: italic;
  src: url('../../assets/fonts/DMSans-Italic-VariableFont_opsz\,wght.ttf')
    format('truetype');
}

:root {
  --light-purple: hsl(254, 88%, 90%);
  --dark-purple: hsl(256, 67%, 59%);
  --light-yellow: hsl(31, 66%, 93%);
  --dark-yellow: hsl(39, 100%, 71%);
  --very-light-grey: hsl(0, 0%, 96%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 7%);
  --cream: hsl(30, 71%, 93%);

  --ff-dm-sans: 'DM Sans', sans-serif;
}

body {
  padding: 32px 16px;
  background: var(--very-light-grey);
  font-family: var(--ff-dm-sans);
}

.grid {
  display: grid;
  gap: 32px;
  max-width: 500px;
  margin-inline: auto;

  &__box {
    border-radius: 10px;
  }
}

.main-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  background: var(--dark-purple);
  text-align: center;

  &__heading {
    margin-bottom: u.rem(24);
    font-size: u.rem(46);
    font-weight: 500;
    line-height: 0.9;
    letter-spacing: u.rem(0.5);
    color: var(--white);

    &--highlighted {
      color: var(--dark-yellow);
    }

    &--italic {
      font-style: italic;
    }
  }

  &__image {
    max-width: 192px;
    margin-bottom: u.rem(8);
  }

  &__text {
    font-size: u.rem(18);
    line-height: 1.1;
    color: var(--light-purple);
  }
}

.platforms-box {
  padding: 14px 16px 16px 16px;
  background: var(--white);

  &__heading {
    font-size: u.rem(28);
    font-weight: 500;
    letter-spacing: u.rem(-2);
    line-height: 0.9;
    color: var(--black);
  }
}

.platforms-illustration {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 311px;
  margin-bottom: u.rem(15);

  &__pill {
    display: flex;
    align-items: center;
    gap: 8px;
    flex-shrink: 0;
    padding: 12px;
    padding-right: 20px;
    background: var(--white);
    border-radius: 1000px;
    box-shadow: -10px 15px 20px rgba(0, 0, 0, 0.05);
  }

  &__icon-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 42px;
    height: 42px;
    background: var(--dark-yellow);
    border-radius: 50%;
  }

  &__icon {
    width: auto;
    height: 24px;
  }

  &__handle {
    margin-bottom: 5px;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: -1px;
    line-height: 1;
    color: var(--black);
  }

  &__follower-count {
    font-size: 11px;
    font-weight: 500;
    line-height: 1;
    color: #c8c8c8;
  }
}

.maintain-box {
  padding-block: 16px 0;
  padding-inline: 16px;
  background: var(--dark-yellow);

  &__heading {
    margin-bottom: u.rem(16);
    font-size: u.rem(28);
    font-weight: 500;
    letter-spacing: u.rem(-2);
    line-height: 0.9;
    color: var(--black);
  }

  &__image {
    aspect-ratio: 210 / 136;
    max-width: 210px;
    object-fit: cover;
    object-position: top;
  }
}

.schedule-box {
  padding-block: 32px 30px;
  padding-inline: 18px;
  background: var(--light-purple);
  font-weight: 500;
  text-align: center;
  color: var(--black);

  &__heading {
    margin-bottom: u.rem(24);
    font-size: u.rem(28);
    letter-spacing: u.rem(-2);
    line-height: 0.9;
  }

  &__image {
    margin-bottom: u.rem(25);
  }

  &__description {
    padding-inline: 5px;
    font-size: u.rem(18);
    line-height: 1.1;
  }
}

.grow-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 24px;
  background: var(--dark-purple);

  &__image {
    width: 230px;
    margin-bottom: u.rem(40);
  }

  &__heading {
    padding-inline: 30px;
    font-size: u.rem(32);
    font-weight: 500;
    text-align: center;
    letter-spacing: u.rem(-1);
    line-height: 0.9;
    color: var(--white);
  }
}

.stats-box {
  padding: 24px;
  background: var(--white);

  &__statistic {
    margin-bottom: u.rem(22);
    font-weight: 500;
    color: var(--black);

    & > * {
      display: block;
    }
  }

  &__value {
    margin-bottom: u.rem(12);
    font-size: u.rem(46);
    line-height: 1;
  }

  &__context {
    font-size: u.rem(18);
    line-height: 1.2;
  }

  &__image {
    max-width: 180px;
  }
}

.content-box {
  padding: 32px;
  background: var(--cream);

  &__heading {
    margin-bottom: u.rem(24);
    font-size: u.rem(32);
    font-weight: 500;
    letter-spacing: u.rem(-1);
    line-height: 0.9;
    color: var(--black);

    &--highlighted {
      font-style: italic;
      color: var(--dark-purple);
    }
  }

  &__image {
    max-width: 190px;
  }
}

.ai-box {
  padding: 24px;
  background: var(--dark-yellow);

  &__heading {
    margin-bottom: u.rem(22);
    font-size: u.rem(32);
    font-weight: 500;
    letter-spacing: u.rem(-1);
    line-height: 0.9;
    color: var(--black);
  }

  &__image {
    max-width: 220px;
  }
}

@media screen and (min-width: 1200px) {
  body {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 83px 0;
  }

  .grid {
    grid-template-columns: repeat(4, 256px);
    grid-template-rows: 324px 59px 156px 246px;
    max-width: unset;
    margin-inline: unset;
  }

  .main-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-area: 1 / 2 / 2 / 4;

    &__heading {
      margin-bottom: 24px;
      font-size: 62px;
      letter-spacing: 0;
      line-height: 0.95;
    }

    &__image {
      margin-bottom: 8px;
    }

    &__text {
      font-size: 18px;
    }
  }

  .platforms-box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    grid-area: 2 / 2 / 4 / 3;
    overflow: hidden;
    padding: 22px 24px;

    &__heading {
      font-size: 32px;
      letter-spacing: -1px;
    }
  }

  .platforms-illustration {
    gap: 14px;
    margin-bottom: 0;
  }

  .maintain-box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    grid-area: 2 / 3 / 4 / 4;
    padding-block: 25px 0;
    padding-inline: 24px;

    &__heading {
      margin-bottom: auto;
      font-size: 32px;
      letter-spacing: -2px;
    }

    &__image {
      aspect-ratio: 208 / 95;
      max-width: unset;
    }
  }

  .schedule-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-area: 1 / 4 / 4 / 5;
    padding: 32px;
    padding-right: 0;
    text-align: start;

    &__heading {
      padding-right: 32px;
      margin-bottom: 22px;
      font-size: 32px;
      letter-spacing: -2px;
      line-height: 0.9;
    }

    &__image {
      width: 224px;
      height: 319px;
      object-fit: cover;
      object-position: left;
      margin-bottom: 25px;
    }

    &__description {
      padding-inline: unset;
      padding-right: 32px;
      font-size: 18px;
    }
  }

  .grow-box {
    flex-direction: row;
    gap: 12px;
    grid-area: 4 / 3 / 5 / 5;
    padding: 23px 24px;

    &__image {
      width: unset;
      height: 100%;
      margin-bottom: 0;
    }

    &__heading {
      padding-inline: 0;
      font-size: 40px;
      text-align: start;
      letter-spacing: 0;
    }
  }

  .stats-box {
    display: flex;
    flex-direction: column;
    grid-area: 4 / 2 / 5 / 3;

    &__statistic {
      margin-bottom: auto;
    }

    &__value {
      margin-bottom: 12px;
      font-size: 62px;
    }

    &__context {
      font-size: 18px;
    }
  }

  .content-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-area: 1 / 1 / 3 / 2;

    &__heading {
      margin-bottom: 24px;
      font-size: 40px;
      letter-spacing: -2px;
    }
  }

  .ai-box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    grid-area: 3 / 1 / 5 / 2;
    padding-right: 12px;

    &__heading {
      padding-right: 40px;
      margin-bottom: unset;
      font-size: 40px;
      letter-spacing: -2px;
    }
  }
}
